.ISAGButton {
  color: white;
  background-color: Transparent;
  background-repeat: no-repeat;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  border: 1px solid rgba(255, 255, 255, 0.7);
  border-radius: 12px;
  margin: 0;
  padding: 20px;
  font-size: 2em;
  height: 100px;
  width: auto;
  min-width: min-content;
  min-height: 0;
  margin-right: 20px;
  text-decoration: none;
}

.ISAGButton:hover {
  background-color: rgba(255, 255, 255, 0.7);
  color:rgb(0, 10, 0);
  transition: 0.25s;
}
